import React from 'react'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';

function About() {
  return (
    <Card sx={{backgroundColor: 'white'}}>
      <CardContent>
        <Typography gutterBottom variant='h4'>About</Typography>
        <Typography variant='body1'>Hi! I'm currently a UC Santa Barbara Class of 2022 student studying Computer Science. 
        I have a background in C++ and web development. Check out some of my stuff or reach out!</Typography>
      </CardContent>
    </Card>
  )
}

export default About