import React from 'react'
import Box from '@mui/material/Box'
import Container from '@mui/material/Container'
import Grid from '@mui/material/Grid'
import Link from '@mui/material/Link'
import Typography from '@mui/material/Typography'
import GitHubIcon from '@mui/icons-material/GitHub';

function Footer() {
  return (
    <footer>
        <Container sx={{ margin: '75px 0 0 0'}}></Container>
        <Container maxWidth='100%' sx={{position:'fixed', bottom: '0px',}}>
          <Grid container spacing={0}>
            <Grid item xs={12} sm={6}>
              <Box sx={{textAlign:'center', margin: '5px 0'}}>
                <Typography variant='body2' sx={{color: 'white'}}>Created by Sean Oh</Typography>
              </Box>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Box sx={{textAlign:'center', margin: '5px 0'}}>
                <Link href='https://github.com/seannoh/' sx={{color:'black'}}><GitHubIcon></GitHubIcon></Link>
              </Box>
            </Grid>

          </Grid>
        </Container>
    
    </footer>
  )
}

export default Footer